import {AfterViewInit, Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {CollectionViewer, DataSource} from "@angular/cdk/collections";
import {ComponentsService, DatabaseService, Mycotoxins, PaginatedDatabaseList} from "../../../../../generated/api";
import {BehaviorSubject, combineLatest, forkJoin, Observable, Subject} from "rxjs";
import {debounceTime, first, map, shareReplay, startWith, switchMap, takeUntil, tap} from "rxjs/operators";
import {MatPaginator, PageEvent} from "@angular/material/paginator";
import {LanguageService} from "../../../../services/language.service";
import {ApiDataIdx, DataService} from "../../../../services/data.service";
import {FormControl, FormGroup} from "@angular/forms";
import {Sort, SortDirection} from "@angular/material/sort";
import type {ColumnOption} from "csv-stringify";
import {fromPromise} from "rxjs/internal-compatibility";
import {TranslateService} from "@ngx-translate/core";
import {LoginService} from "../../../../services/login.service";
import {Router} from "@angular/router";

interface Row {
    readonly date: string | null;
    readonly component: string | null;
    readonly customer: string;
    readonly country: string;
    readonly inclusionRate: string | null;
    readonly risk: string | null;
    readonly bwFactor: string | null;
    readonly fcrFactor: string | null;
    readonly link: string[] | null;
    readonly myco: Record<string, string>
}

interface PageSelection {
    readonly page: number;
    readonly pageSize: number;
}

interface DateFilter {
    readonly min: Date | null;
    readonly max: Date | null;
}

@Component({
    selector: 'app-data-list',
    templateUrl: './data-list.component.html',
    styleUrls: ['./data-list.component.scss']
})
export class DataListComponent implements OnInit, AfterViewInit, OnDestroy {

    private destroyed$ = new Subject();
    private loading$ = new BehaviorSubject(false);
    pageSelection$ = new BehaviorSubject<PageSelection>({page: 0, pageSize: 10});
    sort$ = new BehaviorSubject<Sort>({
        active: 'date',
        direction: 'desc'
    })

    downloading = false;
    downloadingAll = false;
    loading = false;
    dataSource: DataSource<Row>;
    totalSize = 0;

    private displayedColumnsPreMyco = ['date', 'component', 'customer', 'country']
    private displayedColumnsPreMycoNames = ['DATA_VIEW_HEADER_DATE', 'DATA_VIEW_HEADER_SAMPLE', 'DATA_VIEW_HEADER_CUSTOMER', 'DATA_VIEW_HEADER_COUNTRY']
    private displayedColumnsPostMyco = ['risk']
    private displayedColumnsPostMycoNames = ['DATA_VIEW_HEADER_RISK']

    mycotoxins: Mycotoxins[] = [];
    displayedColumns: string[] = [];
    displayedColumnNames: string[] = [];
    private rows: Observable<Row[]>|null = null;

    longNamesFormControl = new FormControl(false);

    datePickerControls = {
        startDate: new FormControl(),
        endDate: new FormControl()
    } as const;
    datePickerFormGroup = new FormGroup(this.datePickerControls);

    @ViewChild(MatPaginator)
    paginator: MatPaginator;

    constructor(
        private readonly router: Router,
        private readonly loginService: LoginService,
        private readonly databaseService: DatabaseService,
        private readonly componentService: ComponentsService,
        private readonly dataService: DataService,
        private readonly languageService: LanguageService,
        private readonly translateService: TranslateService,
    ) {
    }

    ngOnInit(): void {
        this.loginService.isLoggedIn.pipe(
            first()
        ).subscribe(loggedIn => {
            if (!loggedIn) {
                this.router.navigate(['/']);
            }
        })


        this.dataService.get('mycotoxins').pipe(
            takeUntil(this.destroyed$)
        ).subscribe(mycotoxins => {
            this.mycotoxins = mycotoxins;
            this.displayedColumns = [
                ...this.displayedColumnsPreMyco,
                ...mycotoxins.map(m => m.id),
                ...this.displayedColumnsPostMyco
            ];
            this.displayedColumnNames = [
                ...this.displayedColumnsPreMycoNames,
                ...mycotoxins.map(m => m.text),
                ...this.displayedColumnsPostMycoNames
            ]
        });

        const dateFilter = this.datePickerFormGroup.valueChanges.pipe(
            startWith(0),
            map(() => this.getDatePickerFilter()),
            debounceTime(400),
            takeUntil(this.destroyed$)
        );

        const apiData = combineLatest([this.pageSelection$, this.sort$, dateFilter]).pipe(
            switchMap(([{page, pageSize}, {direction, active: activeSort}, dateFilter]) => {
                this.loading$.next(true);
                return this.getApiData(page, pageSize, activeSort, direction, dateFilter);
            }),
            tap(() => this.loading$.next(false)),
            takeUntil(this.destroyed$),
            shareReplay(1)
        );
        const rows = this.rows = this.getRows(apiData).pipe(
            takeUntil(this.destroyed$),
            shareReplay(1),
        );

        apiData.subscribe(response => {
            this.totalSize = response.count;
        });

        this.loading$.pipe(
            debounceTime(100),
            takeUntil(this.destroyed$)
        ).subscribe(loading => this.loading = loading);

        this.dataSource = {
            connect(collectionViewer: CollectionViewer): Observable<Row[] | ReadonlyArray<Row>> {
                return rows;
            },
            disconnect(collectionViewer: CollectionViewer) {

            }
        }
    }

    ngAfterViewInit() {
    }

    pageEvent(event: PageEvent) {
        this.pageSelection$.next({
            ...this.pageSelection$.getValue(),
            page: event.pageIndex,
            pageSize: event.pageSize
        });
    }

    downloadPage() {
        this.doDownload(this.rows, false);
    }

    downloadAll() {
        let destroyed = false;
        this.destroyed$.subscribe(() => {
            console.log("now destroyed")
            destroyed = true
        });
        const promise = (async () => {
            let page = 0;
            const {active: activeSort, direction: sortDirection} = this.sort$.getValue();
            const longNames = this.longNamesFormControl.value as boolean;
            const currentLanguage = await this.languageService.currentLanguage.pipe(first(), takeUntil(this.destroyed$)).toPromise();
            const dataIdx = await this.dataService.dataIdx.pipe(first(), takeUntil(this.destroyed$)).toPromise();
            const mycotoxins = await this.dataService.get('mycotoxins').pipe(first(), takeUntil(this.destroyed$)).toPromise();
            const dateFilter = this.getDatePickerFilter();
            const rows: Row[] = [];
            while (true) {
                if (destroyed) return;

                const apiData = await this.getApiData(page, 100, activeSort, sortDirection, dateFilter).pipe(takeUntil(this.destroyed$)).toPromise();
                const runRows = this.createRows(apiData, longNames, currentLanguage, dataIdx, mycotoxins);
                rows.push(...runRows);
                if (apiData.next == null) break;
                page++;
            }
            if (destroyed) return;
            return rows;
        })();
        this.doDownload(fromPromise(promise), true);
    }

    private getDatePickerFilter(): DateFilter {
        return {
            min: this.datePickerControls.startDate.value,
            max: this.datePickerControls.endDate.value
        };
    }

    private doDownload(csvSource: Observable<Row[]>, all: boolean) {
        if (this.downloading || this.downloadingAll) return;

        this[all ? 'downloadingAll' : 'downloading'] = true;
        return forkJoin({
            csvStuff: fromPromise(import("../../../../util/csv-stuff")),
            rows: csvSource.pipe(first()),
            displayedColumnNames: this.translateService.get(this.displayedColumnNames)
        }).pipe(
            switchMap(({csvStuff: {FileSaver, csvStringify}, rows, displayedColumnNames}) => {
                const columns: ColumnOption[] = this.displayedColumnNames.map((header, index) => {
                    return {
                        header: displayedColumnNames[header],
                        key: this.displayedColumns[index]
                    }
                });
                const fixedRows = rows.map(row => {
                    const res = {
                        ...row.myco
                    };
                    for (const c of [...this.displayedColumnsPreMyco, ...this.displayedColumnsPostMyco]) {
                        res[c] = row[c];
                    }
                    return res;
                });
                const subject = new Subject<void>();
                csvStringify(fixedRows, {
                    header: true,
                    columns
                }, (err, result) => {
                    if (err) {
                        subject.error(err);
                    } else {
                        const blob = new Blob([result], {type: "text/csv;charset=utf-8"})
                        FileSaver.saveAs(blob, "Risk Data.csv");
                        subject.next();
                        subject.complete();
                    }
                });
                return subject;
            }),
            takeUntil(this.destroyed$)
        ).subscribe({
            next: () => {
                this[all ? 'downloadingAll' : 'downloading'] = false;
            },
            error: err => {
                this[all ? 'downloadingAll' : 'downloading'] = false;
                console.error('Failed downloading', err);
            }
        })
    }

    private getRows(apiData: Observable<PaginatedDatabaseList>) {
        return combineLatest([this.longNamesFormControl.valueChanges.pipe(startWith(this.longNamesFormControl.value as boolean)), this.languageService.currentLanguage, apiData, this.dataService.dataIdx, this.dataService.get('mycotoxins')]).pipe(
            map(([longNames, currentLanguage, dataEntries, dataIdx, mycotoxins]) => {
                return this.createRows(dataEntries, longNames, currentLanguage, dataIdx, mycotoxins);
            }),
        );
    }

    private createRows(apiData: PaginatedDatabaseList, longNames: boolean, currentLanguage: string, dataIdx: ApiDataIdx, mycotoxins: Mycotoxins[]): Row[] {
        return apiData.results.map<Row>(entry => {
            const createdAtTS = entry.assessment?.created_at == null ? null : Date.parse(entry.assessment.created_at);
            const myco: Record<string, string> = {};
            for (const m of mycotoxins) {
                for (const contamination of entry.contaminations ?? []) {
                    if (contamination.mycotoxin === m.id) {
                        myco[m.id] = contamination.value?.toLocaleString(currentLanguage) ?? '-';
                        break;
                    }
                }
            }
            return ({
                customer: entry.assessment.customer_name,
                country: dataIdx.countries.get(entry.assessment.country)?.text ?? '-',
                component: entry.component == null ? '-' : longNames ? dataIdx.components.get(entry.component)?.text ?? entry.component : entry.component,
                inclusionRate: entry.inclusion_rate?.toLocaleString(currentLanguage) ?? '-',
                risk: entry.risk == null ? '-' : longNames ? dataIdx.levels.get(entry.risk)?.text ?? entry.risk : entry.risk,
                bwFactor: entry.bw_factor != null ? parseFloat(entry.bw_factor).toLocaleString(currentLanguage, {
                    maximumFractionDigits: 3
                }) : '-',
                fcrFactor: entry.fcr_factor != null ? parseFloat(entry.fcr_factor).toLocaleString(currentLanguage, {
                    maximumFractionDigits: 3
                }) : '-',
                date: createdAtTS == null || isNaN(createdAtTS) ? '-' : new Date(createdAtTS).toLocaleString(currentLanguage, {
                    year: "numeric",
                    month: "2-digit",
                    day: "2-digit"
                }),
                link: undefined, //['/assessment', '123', '2341q'],
                myco
            });
        })
    }

    private getApiData(page: number, pageSize: number, sort: string, sortDirection: SortDirection, dateFilter: DateFilter): Observable<PaginatedDatabaseList> {
        let dbSort: string | null = null;
        switch (sort) {
            case 'date':
                dbSort = 'assessment__created_at';
                break;
            case 'risk':
                dbSort = 'risk';
                break;
            case 'component':
                dbSort = 'component';
                break;
        }

        return this.databaseService.databaseList(
            undefined, dateFilter?.min?.toISOString(), undefined, dateFilter?.max?.toISOString(),
            dbSort == null ? undefined : `${sortDirection === 'desc' ? '-' : ''}${dbSort}`,
            page + 1, pageSize
        );
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
